.navbar {
    background-color: #1f5156;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 20;
}

.nav-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 80px;
    max-width: 1500px;
}

.nav-logo {
    color: #f5b921;
    align-items: center;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    flex-grow: 1;
}

.nav-menu {
    display: flex;
    list-style: none;
    text-align: center;
    margin-right: 2rem;
}

.nav-links {
    color: #fff;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    border-bottom: 3px solid transparent;
}

.fa-code {
    margin-left: 1rem;
}

.nav-item {
    line-height: 40px;
    margin-right: 1rem;
    position: relative;
}

.nav-item:after {
    content: "";
    display: block;
    height: 3px;
    width: 0;
    background: transparent;
    transition: width 0.7s ease, background-color 0.5s ease;
}

.nav-item:hover:after {
    width: 100%;
    background: #ffdd40;
}

.nav-item .active {
    color: #ffdd40;
    border: 1px solid #ffdd40;
}

.nav-icon {
    display: none;
}

@media screen and (max-width: 960px) {
    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 50%;
        border-top: 1pxsolid #fff;
        position: absolute;
        top: 80px;
        left: -110%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        background: #1f5156;
        left: 0px;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-item .active {
        color: #ffdd40;
        border: none;
    }

    .nav-links {
        padding: 0.5rem;
        width: 100%;
        display: table;
    }

    .nav-icon {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(0%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
        color: #ffdd40;
        padding-left: 5%;
    }
    .nav-logo{
        padding-left: 20%;
        font-size: 1.5rem;
    }
    .dropbtn{
        padding-top: 0.7rem;
        padding-bottom: 1.5rem;

    }
}

/* Style the dropdown container */
.dropdown {
    position: relative;
    display: inline-block;
}

/* Style the dropdown button */
.dropbtn {
    background-color: #1f5156;
    color: white;
    text-decoration: none;
    padding-bottom: 0.25rem;
    height: 100%;
    border-bottom: 3px solid transparent;
    border: none;
    font-size: 1.2rem;
}

/* Style the dropdown content */
.dropdown-content {
    padding-top: 15px;
    display: none;
    position: absolute;
    background-color: #1f5156;
    min-width: 13rem;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
}

/* Style the dropdown content links */
.dropdown-content a {
    color: white;
    padding: 0.5rem 1rem;
    text-decoration: none;
    display: block;
}

/* Show the dropdown content when the container is hovered */
.dropdown:hover .dropdown-content {
    display: block;
}


/* Style for the down arrow icon */
.fa-chevron-down {
    margin-left: 0.5rem;
}