*{
  margin: 0;
  padding: 0;
}
html{
  scroll-behavior: smooth;
}

body{
  background-color: white;
}

.main{
  display: flex;
  height: 800px;
  padding-top: 80px;
  background-color: white;
}



.main-images img{
  width: 600px;
  height: auto;
  margin-left: 200px;
}

.main-text{ 
  font-size: 40px;
  padding-top: 300px;
  padding-left: 100px;
  font-weight: bold;
  font-family: 'Georgia', serif;
}

.result{
  padding-top: 80px;
  background-color: #1D1D1F;
  width: 60%;
  margin-left: 20%;
}
.result-images{
  display: flex;
  justify-content: center;
  align-items: center;
}

.result-images img{
  width: 500px;
  height: auto;
}
.result-text{
  font-family: "san-serif";
  font-size: 80px;
  font-weight: bold;
  color: #0985df;
  flex: 1; /* This will make the text container take up the rest of the space */
  padding: 20px; /* Add some padding around the text */
  text-align: left;
  margin-left: 0px;
}

.use{
  padding-top: 80px;
  height: 1500px;
  background-color: #1D1D1F;
  background-size: cover;
  background-position: center;
  position: relative;
  width: 60%;
  margin-left: 20%;
}
.use-images{
  display: flex;
  justify-content: center;
  align-items: center;
}

.use-video{
  width: 100%;
  height: 30%;
  justify-content: center;
  align-items: center;
}
.use-images img{
  padding-top: 100px;
  width: 80%;
  height: auto;
}

.place{
  padding-top: 80px;
  background-color: #1D1D1F;
  width: 60%;
  margin-left: 20%;
}
.place-text{
  font-family: "san-serif";
  font-size: 50px;
  font-weight: bold;
  color: #0985df;
  flex: 1; /* This will make the text container take up the rest of the space */
  padding: 20px;
  padding-bottom: 100px;
  text-align: center;
  margin-left: 0px;
}
.carousel__image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 10%;
  width: 80%;
  height: auto;
}
.carousel__image {
  max-width: 100%;
  max-height: 100%;
}

#large-text{
  font-size: 70px;
  color: white;
}
.contact{
  height: 200px;;
  background-color: black;
  color: grey;
  font-weight: 500;
  background-size: cover;
  background-position: center;
  text-align: left;
  width: 100%;
  box-sizing: border-box;
}
.nums{
  display: block;
}
@media screen and (max-width: 960px) {
  .main{
    flex-direction: column;
    align-items: center;
    display: flex;
    height: 600px;
    padding-top: 80px;
    background-color: white;
  }
  
  .main-images img{
    margin-left: 0;
    width: 100%;
    height: auto;
  }
  
  .main-text{ 
    font-size: 24px;
    padding-top: 50px;
    padding-left: 0;
    font-weight: bold;
    font-family: 'Georgia', serif;
  }

  .result{
    padding-top: 80px;
    background-color: #1D1D1F;
    width: 100%;
    margin-left: 0;
  }
  .result-images{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .result-images img{
    width: 100%;
    height: auto;
  }
  .result-text{
    font-family: "san-serif";
    font-size: 40px;
    font-weight: bold;
    color: #0985df;
    flex: 1; /* This will make the text container take up the rest of the space */
    padding: 20px; /* Add some padding around the text */
    text-align: left;
    margin-left: 0px;
  }
  .use{
    padding-top: 80px;
    height: 600px;
    background-color: #1D1D1F;
    background-size: cover;
    background-position: center;
    position: relative;
    width: 100%;
    margin-left: 0;
  }
  .use-images{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .use-video{
    width: 100%;
    height: 30%;
    justify-content: center;
    align-items: center;
  }
  .use-images img{
    padding-top: 100px;
    width: 80%;
    height: auto;
  }
  .place{
    padding-top: 80px;
    background-color: #1D1D1F;
    width: 100%;
    margin-left: 0;
  }
  .place-text{
    font-family: "san-serif";
    font-size: 50px;
    font-weight: bold;
    color: #0985df;
    flex: 1; /* This will make the text container take up the rest of the space */
    padding: 20px;
    padding-bottom: 100px;
    text-align: center;
    margin-left: 0px;
  }
  #large-text{
    font-size: 50px;
    color: white;
  }
}